/* */
:root {
  --theme-color-1: #2196f3;
  --theme-color-2: rgba(33, 150, 243, 0.1);
  --md-gold-500: #A99E7E; }

html {
  font-size: 10px;
  touch-action: manipulation; }

body {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-touch-callout: none;
  text-size-adjust: none;
  user-select: none; }

a,
a:hover {
  text-decoration: none; }

input,
textarea,
select,
button {
  margin: 0;
  outline: none;
  border: none; }

[required] {
  box-shadow: none; }

*,
*::before,
*::after {
  box-sizing: border-box;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  font-smooth: always; }

img {
  pointer-events: none; }
